.blind{filter:blur(5px) brightness(0.7);transition:all .25s cubic-bezier(0.645, 0.045, 0.355, 1)}.numbered-heading{display:flex;align-items:center;counter-increment:nheading 1;font-family:"Fira Mono",monospace;margin:10px 0 40px}.numbered-heading::before{content:"0" counter(nheading) ".";margin-right:5px;color:#64ffda;font-size:22px}.numbered-heading::after{content:"";display:block;position:relative;top:-5px;width:300px;height:1px;margin-left:20px;background-color:#112240}.inner{display:grid;grid-template-columns:3fr 2fr;gap:50px}.green{color:#64ffda}#content{max-width:1000px;margin:0 auto}#content section{display:flex;justify-content:center;flex-direction:column;align-items:flex-start;min-height:100vh;padding:0 2rem}#content section.welcome h1{font-family:"Fira Mono",monospace;font-size:clamp(14px,5vw,16px);font-weight:400;color:#64ffda;margin:0 0 30px 4px}#content section.welcome h2,#content section.welcome h3{font-size:clamp(40px,8vw,50px);font-weight:600;margin:10px 0 0}#content section.welcome h2{color:#ccd6f6}#content section.welcome p{margin:20px 0 0;font-size:18px;max-width:540px}#content section.welcome a{margin:50px 0 0;font-size:16px}#content section .presentation div p{font-size:18px;margin-bottom:15px}#content section .presentation .skills-list{display:grid;grid-template-columns:repeat(2, minmax(140px, 200px));gap:0px 10px;padding:0px;margin:20px 0px 40px;overflow:hidden;list-style:none}#content section .presentation .skills-list li{position:relative;margin-bottom:10px;padding-left:20px;font-family:"Fira Mono",monospace;font-size:14px}#content section .presentation .skills-list li::before{content:"â–¹";position:absolute;left:0px;color:#64ffda;font-size:14px;line-height:12px}#content section .inner .me{position:relative;width:300px;height:300px;margin:0 auto}#content section .inner .me img{width:300px;height:300px}#content section .inner .me .overlay{position:absolute;top:0;bottom:0;left:0;right:0;height:100%;width:100%;transition:.5s ease;background-color:#64ffda;mix-blend-mode:color}#content section .inner .me .overlay:hover{opacity:0}#content section .inner .me::after{content:"";position:absolute;left:25px;top:25px;border:1px solid #64ffda;width:100%;height:100%;z-index:-1}@media only screen and (max-width: 762px){.inner{display:block}#content section.welcome p{max-width:70%}}